export default {
	data() {
		return {
			sex:1,
			globalUser: {},
		}
	},
	//先获取缓存
	onLoad() {
		var _this = this;
		_this.globalUser = _this.getGlobalUser("globalUser");
		_this.sex = _this.globalUser.sex;
	},
	methods: {
		sexChange(e){
			//获取选择的性别
			this.sex = e.detail.value;
			console.log(this.sex);
		},
		
		formSubmitSex(){
			var _this = this;
			uni.request({
				url:_this.serverUrl+"/user/xxxxxxx",
				data:{
					"userId":_this.globalUser.id,
					"sex":_this.sex
				},
				header:{
					"headerUserId": _this.globalUser.id,
					"headerUserToken": _this.globalUser.userUniqueToken
				},
				method:"POST",
				success(res) {
					console.log(res);
					if(res.data.data.status == 200){
						var userInfo = res.data.data;
						uni.setStorageSync("globalUser",userInfo);
						uni.navigateBack({
							delta:1
						})
					}else{
						uni.showToast({
							title: res.data.msg,
							image: "",
							duration: 2000
						})
					}
					
				}
			})
		}
	}
}
